@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');

:root {
  --blue: #5D3891;
  --white: #F5F5F5;
  --gray: #E8E2E2;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  font-family: 'Ubuntu', sans-serif;
  background-color: var(--gray);
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 10vh;
}

.nav-links {
  width: 40%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
  cursor: pointer;
}

.link {
  padding: 7px 14px;
  color: inherit;
  text-decoration: none;
}

.active {
  border-bottom: 2px solid var(--blue);
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.Scoreboard {
  display: flex;
  gap: 30px;
  margin-top: 10px;
  font-size: larger;
  background-color: var(--white);
  color: var(--blue);
  padding: 10px;
  border-radius: 10px;
}

.cards-container {
  display: grid;
  grid-template-columns: repeat(3, 140px);
  grid-template-rows: repeat(3, 140px);
  border: 2px solid var(--blue);
  border-radius: 10px;
  background-color: var(--gray);
}

.card {
  height: 120px;
  width: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: xx-large;
  justify-self: center;
  align-self: center;
  border-radius: 10px;
}

.card:hover {
  opacity: 0.8;
}

.card img {
  border-radius: 10px;
}

.word {
  height: 120px;
  width: 120px;
  border: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: xx-large;
  justify-self: center;
  align-self: center;
  border-radius: 10px;
  background-color: var(--blue);
  color: var(--white);
}

.word:hover {
  opacity: 0.8;
}